import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
  		status: '',
  		token: localStorage.getItem('token') || '',
  		user : {},
      errors:[],
  },
  mutations: {
    auth_request(state){
	    state.status = 'loading'
	  },
	  auth_success(state, token, user){
		   state.status = 'success'
		   state.token = token
		   state.user = user
       state.errors = null
	  },
	  auth_error(state,errors){
	    state.status = 'error'
      state.errors = errors
	  },
	  logout(state){
	    state.status = ''
	    state.token = ''
    },
    register_success(state){
		   state.status = 'registered'
       state.errors = null
	  },
  },
  actions: {
    login({commit}, user){
      return new Promise((resolve, reject) => {
	       commit('auth_request')
	            axios({url: 'http://127.0.0.1:8000/api/login', data: user, method: 'POST' })
	            .then(resp => {
	                const token = resp.data.access_token
	                const user = resp.data.user
	                localStorage.setItem('token', token)
	                axios.defaults.headers.common = {'Authorization': `Bearer ${token}`}
	                commit('auth_success', token, user)
	                resolve(resp)
	            })
	            .catch(err => {
                  let errors = err.response.data
	                commit('auth_error',errors)
	                localStorage.removeItem('token')
	                reject(err)
	            })
	        });
    },
    logout({commit}){
		    return new Promise((resolve, reject) => {
            axios({url: 'http://127.0.0.1:8000/api/logout', method: 'POST' })
            .then(resp => {
              commit('logout')
              localStorage.removeItem('token')
              delete axios.defaults.headers.common['Authorization']
              resolve(resp)
            })

		    })
    },
    register({commit}, user) {
      return new Promise((resolve, reject) => {
        axios({url:'http://127.0.0.1:8000/api/register', data:user, method:'POST'})
        .then(resp => {
            commit('register_success')
            resolve(resp)
        })
        .catch(err => {
            console.log(err)
        })
      })
    }
  },
  getters: {
    isLoggedIn: state => !!state.token,
    authStatus: state => state.status,
  },
});
