<template>
  <nav class="main-nav">
    <div class="menu-icon"
        v-bind:class="{ active: showMobileMenu }"
        v-on:click="showMobileMenu = !showMobileMenu">
      <div class="hamburger"></div>
    </div>
    <ul class="link-wrapper" v-if="showMobileMenu">
      <li class="link-item">
        <router-link to="/">Home</router-link>
      </li>
      <li class="link-item">
        <router-link to="/register">Register</router-link>
      </li>
      <li class="link-item" v-if="this.$store.getters.isLoggedIn">
        <a href="#" @click.prevent="logout">Logout</a>
      </li>
    </ul>
  </nav>
</template>

<style>
.main-nav {
  overflow: hidden;
}
.link-wrapper {
  background: #1b1b1b;
  width: 100vw;
  list-style: none;
  padding:0;
  margin:0;
  padding-top: 18%;
  position: absolute;
  z-index: 2;
}
.link-item {
  display: block;
  margin:5%;
}
.link-item a {
  color:#f9f9f9;
  padding:10px 0;
  font-weight: bold;
  text-decoration: none;
}
/*navigation icon styles*/
.menu-icon {
  position: absolute;
  top:4%;
  left:4%;
  width: 32px;
  height:32px;
  z-index: 3;
  cursor: pointer;
}
.hamburger {
  width: 24px;
  height: 2px;
  background: #e1c79b;
  position: absolute;
  top:4%;
  left:4%;
  transition: .3s;
}
.hamburger::before,
.hamburger::after {
  content: '';
  position: absolute;
  background: #e1c79b;
  width: 24px;
  height: 2px;
  transition: .3s;
}
.hamburger::before {
  top: -8px;
}
.hamburger::after {
  top:8px;
}
.menu-icon.active .hamburger {
  background: rgba(0, 0, 0, 0);
}
.menu-icon.active .hamburger::before {
  top:0;
  transform: rotate(45deg);
}
.menu-icon.active .hamburger::after {
  top:0;
  transform: rotate(135deg);
}

</style>

<script>
export default {
  name: 'main-nav',
  data: function () {
      return {
          showMobileMenu: false,
      }
  },
  methods:{
    logout() {
      this.$store.dispatch('logout').then(() => this.$router.push('/'))
    }
  }
}
</script>
